import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import Paper from "@material-ui/core/Paper";
import InputBase from "@material-ui/core/InputBase";
import IconButton from "@material-ui/core/IconButton";
import SearchIcon from "@material-ui/icons/Search";
import { useShips } from "../../../context/ships";

const useStyles = makeStyles((theme) => ({
  root: {
    padding: "2px 2px",
    display: "flex",
    alignItems: "center",
    width: 400,
    float: "right",
    marginBottom: "10px",
  },
  input: {
    marginLeft: theme.spacing(1),
    flex: 1,
  },
  iconButton: {
    padding: 10,
  },
  divider: {
    height: 28,
    margin: 4,
  },
}));

export default function ShipsSearchBar() {
  const classes = useStyles();
  const ships_context = useShips();

  const handleSearch = (value) => {
    let filtered;
    if (value === "") {
      filtered = ships_context.defaultShips;
    } else {
      let regex = new RegExp(value, "i");
      filtered = ships_context.shipsList.filter((item) =>
        regex.test(item.name)
      );
    }

    ships_context.setShipsList(filtered);
  };

  return (
    <Paper component="form" className={classes.root}>
      <InputBase
        onChange={(e) => handleSearch(e.target.value)}
        className={classes.input}
        placeholder="Search Ships"
        inputProps={{ "aria-label": "search ships" }}
      />
      <IconButton
        type="submit"
        className={classes.iconButton}
        aria-label="search"
      >
        <SearchIcon />
      </IconButton>
    </Paper>
  );
}
