import React from "react";
import { useShips } from "../../../context/ships";
import { useSchedule } from "../../../context/schedule";
import { makeStyles } from "@material-ui/core/styles";
import { Row } from "reactstrap";
import ShipCards from "../Common/ShipCard";
import LinearProgressBarWithLabel from "../Common/LinearProgressBarWithLabel";
import "./grid.css";
import { Typography } from "@material-ui/core";

const useStyles = makeStyles((theme) => ({
  root: {
    display: "flex",
    flexWrap: "wrap",
    overflow: "hidden",
    backgroundColor: theme.palette.background.paper,
  },
}));

export default function ShipsGrid(props) {
  const classes = useStyles();
  const ships_context = useShips();
  const schedule_context = useSchedule();

  return (
    <div className={classes.root}>
      <Row
        className="ml-1"
        style={{
          minWidth: "100%",
          overflowY: "hidden",
          overflowX: "hidden",
          justifyContent:
            ships_context.shipsList.length === 0 ? "center" : "space-evenly",
        }}
      >
        {ships_context.shipsList.length > 0 && (
          <Row
            style={{
              minHeight: "450px",
              minWidth: "100%",
              marginLeft: "80px",
              width: "100%",
              maxHeight: "450px",
              paddingRight: "30px",
              boxSizing: "content-box",
              overflowY: "scroll",
            }}
          >
            {ships_context.shipsList.map((ships) => (
              <ShipCards
                ship={ships.name}
                key={ships.id}
                ships={ships}
                handleSelectedShipSchedule={(ships) =>
                  schedule_context.handleShipScheduleList(ships)
                }
              />
            ))}
          </Row>
        )}
        {ships_context.shipsList.length === 0 && (
          <div className="text-center col-12" style={{ color: "#ccc" }}>
            {ships_context.isShipsLoading && (
              <LinearProgressBarWithLabel title="Loading Ships..." />
            )}
            {!ships_context.isShipsLoading && (
              <Typography variant="h5">No Ships Found</Typography>
            )}
          </div>
        )}
      </Row>
    </div>
  );
}
