import React, { useState, useEffect } from "react";
import DateFnsUtils from "@date-io/date-fns";
import { useAuth } from "../../../context/auth";
import { useUser } from "../../../context/user";
import { useTravel } from "../../../context/travel";
import { FormControlLabel, Switch, TextField, Button } from "@material-ui/core";
import { Container, Row, Col, Card, CardBody, Form } from "reactstrap";
import GenderSelect from "./Fields/GenderSelect";
import AccommodationType from "./Fields/AccommodationType";
import API from "../../../utils/Api";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faArrowLeft,
  faArrowRight,
  faCheck,
  faInfoCircle,
  faPeopleArrows,
  faSave,
  faShip,
  faUsers,
} from "@fortawesome/free-solid-svg-icons";
import { Typography } from "@material-ui/core";
import Swal from "sweetalert2";

const UpdatePreBoardForm = (props) => {
  const dateFns = new DateFnsUtils();
  const authToken = useAuth().authTokens;
  const user_context = useUser();
  const travel_context = useTravel();
  const [isProcessing, setIsProcessing] = useState(false);
  const [isEdit, setIsEdit] = useState(false);
  const [isDisabled, setIsDisabled] = useState(true);

  // Steps
  const [steps, setSteps] = useState("1");

  // Personal
  const [passengerID, setPassengerID] = useState("");
  const [firstName, setFirstName] = useState("");
  const [lastName, setLastName] = useState("");
  const [gender, setGender] = useState("");
  const [age, setAge] = useState("");
  // Contact
  const [email, setEmail] = useState("");
  const [mobileNumber, setMobileNumber] = useState("");
  // Address
  const [zipcode, setZipcode] = useState("");
  const [specificAddress, setSpecificAddress] = useState("");

  // Other Details
  const [childCompanion, setChildCompanion] = useState("");
  const [relativeName, setRelativeName] = useState("");
  const [relativeContact, setRelativeContact] = useState("");
  const [travelHistory, setTravelHistory] = useState("");
  const [seatNumber, setSeatNumber] = useState("");
  const [parkingNumber, setParkingNumber] = useState("");
  const [accommodationType, setAccommodationType] = useState("");

  const saveProfileHandler = (e) => {
    e.preventDefault();
    handleEdit();
    let postData = {
      first_name: firstName,
      last_name: lastName,
      gender: gender,
      age: age,
      email: email,
      mobile: mobileNumber,
      zipcode: parseInt(zipcode),
      specific_address: specificAddress,
    };

    API.post("auth/profile/update", postData, {
      headers: {
        Authorization: "bearer " + authToken.access_token,
      },
    }).then((result) => {
      if (result.data.status) {
        handleEdit();
        user_context.getUserDetails();
      }
    });
  };

  const savePreBoarding = (e) => {
    e.preventDefault();
    setIsProcessing(true);
    props.setIsProcessing(true);

    let postData = {
      // Personal
      first_name: firstName,
      last_name: lastName,
      gender: gender,
      age: age,
      email: email,
      mobile: mobileNumber,
      zipcode: parseInt(zipcode),
      specific_address: specificAddress,

      // Travel Details
      origin: travel_context.selectedTravel.fromLocation,
      destination: travel_context.selectedTravel.toLocation,
      previous_travel: travelHistory,

      // Travel Dates
      departure: dateFns.format(
        new Date(travel_context.selectedTravel.fromDate),
        "Y-M-dd H:m:s"
      ),
      arrival: dateFns.format(
        new Date(travel_context.selectedTravel.toDate),
        "Y-M-dd H:m:s"
      ),

      accommodation_type: accommodationType,

      // Transport Details
      travel_id: travel_context.selectedTravel.id,
      transport_no: parseInt(
        travel_context.selectedTravel.vehicle_details.transport_no
      ), // voyage number
      route: travel_context.selectedTravel.ships[0].routes,
      transport_type:
        travel_context.selectedTravel.ships[0].transportation_category_id,
      seat_no: seatNumber,
      parking_space_no: parkingNumber,
      ship_name: travel_context.selectedTravel.ships[0].id,

      // Child Companion
      companion_name: childCompanion,
      //Relative
      relative_name: relativeName,
      relative_contact: relativeContact,
      scheduled_transportation_id: travel_context.selectedTravel.scheduled_transportation_id,
    };

    API.put(`auth/profile/update-boarding/${passengerID}`, postData, {
      headers: {
        Authorization: "bearer " + authToken.access_token,
      },
    })
      .then((result) => {
        let action;
        if (result.data.status) {
          action = Swal.fire(
            "Transaction Completed!",
            "Pre-boarding Updated",
            "success"
          );
        } else {
          action = Swal.fire(
            "Transaction Failed!",
            "Pre-boarding Update Failed",
            "error"
          );
        }

        travel_context.getTravelHistory();
        setIsProcessing(false);
        props.setIsProcessing(false);

        action.then(() => {
          props.toggle();
        });
      })
      .catch((e) => {
        console.log(e)
        Swal.fire("Transaction Failed!", "Pre-boarding Update Failed", "error");
        setIsProcessing(false);
        props.setIsProcessing(false);
      });
  };

  const handleEdit = () => {
    setIsEdit(!isEdit);
    if (isDisabled) {
      setIsDisabled(null);
    } else {
      setIsDisabled(true);
    }
  };

  const goto = (step) => {
    setSteps(step);
  };

  useEffect(() => {
    let profile = user_context.userData;
    let travel = travel_context.selectedTravel;
    let relatives = travel_context.selectedTravel.profile.relatives;

    setPassengerID(travel.id);
    setFirstName(profile.first_name);
    setLastName(profile.last_name);
    setAge(profile.age);
    setGender(profile.gender);
    setMobileNumber(profile.mobile);
    setEmail(profile.email);
    setZipcode(profile.address.zipcode);
    setSpecificAddress(profile.address.specific_address);

    if(travel.companions.length > 0){
      setChildCompanion(travel.companions[0].name);
    }
        
    setTravelHistory(travel.previous_place_travel);
    setParkingNumber(travel.parking_space_no);
    setAccommodationType(travel.accommodation_type);
    setSeatNumber(travel.seat_no);
    setRelativeName(relatives[relatives.length - 1].name);
    setRelativeContact(relatives[relatives.length - 1].contact);
    return () => {};
  }, []);

  return (
    <Container fluid className="pt-2">
      {steps === "1" && (
        <div>
          <Typography className="ml-2 d-inline">
            <FontAwesomeIcon icon={faInfoCircle} /> Click next if details are
            correct or
          </Typography>
          <FormControlLabel
            className="m-0 d-inline"
            control={
              <Switch checked={isEdit} onChange={handleEdit} color="primary" />
            }
            labelPlacement="start"
            label="Edit"
          />
          <Form onSubmit={(e) => saveProfileHandler(e)} autoComplete="off">
            <Row>
              <Col xs={12}>
                <Row>
                  <Col xs={12}>
                    <Card className="ml-2 mr-2">
                      <CardBody className="pt-1">
                        <Row>
                          <Col md={6}>
                            <div>
                              <TextField
                                name="first_name"
                                label="First Name"
                                value={firstName}
                                onChange={(e) => setFirstName(e.target.value)}
                                fullWidth
                                required
                                disabled={isDisabled}
                              />
                            </div>
                            <div>
                              <TextField
                                name="last_name"
                                label="Last Name"
                                value={lastName}
                                onChange={(e) => setLastName(e.target.value)}
                                fullWidth
                                required
                                disabled={isDisabled}
                              />
                            </div>
                            <div>
                              <TextField
                                type="number"
                                name="age"
                                label="Age"
                                value={age}
                                onChange={(e) => setAge(e.target.value)}
                                fullWidth
                                required
                                disabled={isDisabled}
                              />
                            </div>
                            <div>
                              <GenderSelect
                                gender={gender}
                                setGender={setGender}
                                disabled={isDisabled}
                              />
                            </div>
                          </Col>
                          <Col md={6}>
                            <div>
                              <TextField
                                name="mobile"
                                type="number"
                                label="Mobile Number"
                                value={mobileNumber || ""}
                                onChange={(e) =>
                                  setMobileNumber(e.target.value)
                                }
                                fullWidth
                                required
                                disabled={isDisabled}
                              />
                            </div>
                            <div>
                              <TextField
                                type="email"
                                name="email"
                                label="Email Address"
                                onChange={(e) => setEmail(e.target.value)}
                                value={email}
                                fullWidth
                                disabled={isDisabled}
                              />
                            </div>
                            <div>
                              <TextField
                                name="zipcode"
                                label="Zipcode"
                                value={zipcode || ""}
                                onChange={(e) => setZipcode(e.target.value)}
                                fullWidth
                                required
                                disabled={isDisabled}
                              />
                            </div>
                            <div>
                              <TextField
                                label="Complete Address"
                                name="complete_address"
                                value={specificAddress}
                                onChange={(e) =>
                                  setSpecificAddress(e.target.value)
                                }
                                fullWidth
                                required
                                disabled={isDisabled}
                              />
                            </div>
                          </Col>
                        </Row>
                      </CardBody>
                    </Card>
                  </Col>
                </Row>
              </Col>
            </Row>
            {isEdit && (
              <Row className="mt-3 ml-2">
                <Button
                  type="submit"
                  disabled={isDisabled}
                  style={{ minWidth: "120px" }}
                  className="bg-primary text-white"
                >
                  <FontAwesomeIcon icon={faSave} className="mr-1" />
                  Save
                </Button>
              </Row>
            )}

            {!isProcessing && (
              <div>
                {!isEdit && (
                  <Row className="mt-3 ml-2">
                    <Button
                      type="button"
                      style={{ minWidth: "120px" }}
                      className="bg-primary text-white"
                      onClick={() => goto("2")}
                    >
                      Next{" "}
                      <FontAwesomeIcon icon={faArrowRight} className="ml-1" />
                    </Button>
                  </Row>
                )}
              </div>
            )}
          </Form>
        </div>
      )}
      {steps === "2" && (
        <div>
          <Form onSubmit={(e) => savePreBoarding(e)} autoComplete="off">
            <Col xs={12}>
              <Row>
                <Col xs={6}>
                  <h6 className="text-primary">
                    <FontAwesomeIcon icon={faUsers} /> Companion Details
                  </h6>
                  <Card className="p-1">
                    <CardBody className="pt-1">
                      <div className="mt-2 mb-2">
                        <TextField
                          label="Child Companion"
                          name="childCompanion"
                          value={childCompanion || ""}
                          helperText="Use comma (,) as separator for multiple child"
                          onChange={(e) => setChildCompanion(e.target.value)}
                          fullWidth
                        />
                        <TextField
                          label="Previous Place Travelled"
                          name="travelHistory"
                          value={travelHistory}
                          onChange={(e) => setTravelHistory(e.target.value)}
                          fullWidth
                          required
                        />
                      </div>
                    </CardBody>
                  </Card>
                </Col>
                <Col xs={6}>
                  <h6 className="text-primary">
                    <FontAwesomeIcon icon={faPeopleArrows} /> Nearest Relative
                  </h6>
                  <Card className="p-1 pt-0 pb-0">
                    <CardBody className="p-1 pb-2 pl-3 pr-3">
                      <div>
                        <TextField
                          label="Name"
                          name="relativeName"
                          value={relativeName || ""}
                          onChange={(e) => setRelativeName(e.target.value)}
                          fullWidth
                          required
                        />
                      </div>
                      <div>
                        <TextField
                          type="number"
                          label="Contact Number"
                          name="relativeContact"
                          value={relativeContact || ""}
                          onChange={(e) => setRelativeContact(e.target.value)}
                          fullWidth
                          required
                        />
                      </div>
                    </CardBody>
                  </Card>
                </Col>
                <Col xs={12}>
                  <h6 className="text-primary mt-2">
                    <FontAwesomeIcon icon={faShip} /> Addtional Transport
                    Details
                  </h6>
                  <Row>
                    <Col md={12}>
                      <Card className="p-1">
                        <CardBody className="pt-1">
                          <Row>
                            <Col md={6} className="pr-4">
                              <div className="mt-2 mb-2">
                                <AccommodationType
                                  accommodationType={accommodationType}
                                  setAccommodationType={setAccommodationType}
                                />
                                <TextField
                                  label="Seat Number"
                                  name="seatNumber"
                                  value={seatNumber}
                                  onChange={(e) =>
                                    setSeatNumber(e.target.value)
                                  }
                                  fullWidth
                                  required
                                />
                              </div>
                            </Col>
                            <Col md={6} className="pl-4 pr-4">
                              <div className="mt-2 mb-2 ml-1">
                                <TextField
                                  label="Parking Number (Optional)"
                                  name="parkingNumber"
                                  value={parkingNumber || ""}
                                  onChange={(e) =>
                                    setParkingNumber(e.target.value)
                                  }
                                  fullWidth
                                />
                              </div>
                            </Col>
                          </Row>
                        </CardBody>
                      </Card>
                    </Col>
                  </Row>
                </Col>
              </Row>
              {!isProcessing && (
                <div className="d-inline">
                  <Row className="mt-3 p-0">
                    <Button
                      type="button"
                      style={{ minWidth: "120px" }}
                      className="bg-primary text-white mr-1 ml-3"
                      onClick={() => goto("1")}
                    >
                      <FontAwesomeIcon icon={faArrowLeft} className="mr-1" />
                      Back{" "}
                    </Button>
                    <Button
                      type="submit"
                      style={{ minWidth: "120px" }}
                      className="bg-primary text-white"
                    >
                      Save
                      <FontAwesomeIcon icon={faCheck} className="ml-1" />
                    </Button>
                  </Row>
                </div>
              )}
            </Col>
          </Form>
        </div>
      )}
    </Container>
  );
};

export default UpdatePreBoardForm;
