import React from "react";
import { useAuth } from "../../../context/auth";
import { useSchedule } from "../../../context/schedule";
import { makeStyles } from "@material-ui/core/styles";
import { Row } from "reactstrap";
import "./grid.css";
import LinearProgressBarWithLabel from "../Common/LinearProgressBarWithLabel";
import ScheduleListCard from "../Common/ScheduleListCard";
import { Typography } from "@material-ui/core";

const useStyles = makeStyles((theme) => ({
  root: {
    display: "flex",
    flexWrap: "wrap",
    overflow: "hidden",
    backgroundColor: theme.palette.background.paper,
  },
}));

export default function SchedulesGrid(props) {
  const classes = useStyles();
  const schedule_context = useSchedule();

  return (
    <div className={classes.root}>
      <Row
        className="ml-1"
        style={{
          minWidth: "100%",
          overflowY: "hidden",
          overflowX: "hidden",
          justifyContent:
          schedule_context.carrierScheduleList.length === 0 ? "center" : "flex-start",
        }}
      >
        {schedule_context.carrierScheduleList.length > 0 && (
          <Row
            style={{
              minHeight: "450px",
              minWidth: "100%",
              marginLeft: "35px",
              width: "100%",
              maxHeight: "450px",
              paddingRight: "30px",
              boxSizing: "content-box",
              overflowY: "scroll",
            }}
          >
            {schedule_context.carrierScheduleList.map((schedule) => (              
              <ScheduleListCard schedule={schedule} key={schedule.id} />
            ))}
          </Row>
        )}
        {schedule_context.carrierScheduleList.length === 0 && (
          <div className="text-center col-12" style={{ color: "#ccc" }}>
            {schedule_context.isScheduleLoading && (
              <LinearProgressBarWithLabel title="Loading Schedules..." />
            )}
            {!schedule_context.isScheduleLoading && <Typography variant="h5">No Schedules Found</Typography>}
          </div>
        )}
      </Row>
    </div>
  );
}
