import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import Card from "@material-ui/core/Card";
import CardActionArea from "@material-ui/core/CardActionArea";
import CardActions from "@material-ui/core/CardActions";
import CardContent from "@material-ui/core/CardContent";
import CardMedia from "@material-ui/core/CardMedia";
import Button from "@material-ui/core/Button";
import Typography from "@material-ui/core/Typography";
import ShipSvg from "../../../assets/ship.svg";
import TodayIcon from "@material-ui/icons/Today";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faAnchor } from "@fortawesome/free-solid-svg-icons";

const useStyles = makeStyles({
  root: {
    minWidth: 250,
    maxWidth: 200,
    maxHeight: 200,
    margin: 15,
  },
});

export default function ShipCards(props) {
  const classes = useStyles();

  return (
    <Card className={classes.root}>
      <CardActionArea>
        <CardMedia
          component="img"
          alt="Ships Blue"
          height="100"
          image={ShipSvg}
          title={props.ship.toUpperCase()}
        />
        <CardContent className="m-0 p-1 bg-primary text-white">
          <Typography noWrap variant="h6" component="h5" fontSize="small">
            <FontAwesomeIcon className="mr-1" icon={faAnchor} />
            {props.ship.toUpperCase()}
          </Typography>
        </CardContent>
      </CardActionArea>
      <CardActions
        className="text-center m-0 p-0"
        style={{ justifyContent: "center" }}
      >
        <Button
          size="small"
          color="primary"
          className="ml-2 mr-2 pt-2 pb-2"
          style={{ minWidth: "100%"}}
          onClick={() => props.handleSelectedShipSchedule(props.ships)}
        >
          <TodayIcon className="mr-1" fontSize="small" /> Schedules
        </Button>
      </CardActions>
    </Card>
  );
}
