import React, { useState, useEffect } from "react";
import { useAuth } from "../../../context/auth";
import { useUser } from "../../../context/user";
import { FormControlLabel, Switch, TextField, Button } from "@material-ui/core";
import { Container, Row, Col, Card, CardBody, Form } from "reactstrap";
import GenderSelect from "./Fields/GenderSelect";
import Swal from "sweetalert2";
import API from "../../../utils/Api";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faSave } from "@fortawesome/free-solid-svg-icons";

const PassengerForm = (props) => {
  const authToken = useAuth().authTokens;
  const user_context = useUser();
  const [isProcessing, setIsProcessing] = useState(false);
  const [isEdit, setIsEdit] = useState(false);
  const [isDisabled, setIsDisabled] = useState(true);


  // Personal
  const [firstName, setFirstName] = useState("");
  const [lastName, setLastName] = useState("");
  const [gender, setGender] = useState("");
  const [age, setAge] = useState("");
  // Contact
  const [email, setEmail] = useState("");
  const [mobileNumber, setMobileNumber] = useState("");
  // Address
  const [zipcode, setZipcode] = useState("");
  const [specificAddress, setSpecificAddress] = useState("");

  const saveProfileHandler = (e) => {
    e.preventDefault();

    props.setIsProcessing(true);
    setIsProcessing(true);

    let postData = {
      first_name: firstName,
      last_name: lastName,
      gender: gender,
      age: age,
      email: email,
      mobile: mobileNumber,
      zipcode: parseInt(zipcode),
      specific_address: specificAddress,
    };

    API.post("auth/profile/update", postData, {
      headers: {
        Authorization: "bearer " + authToken.access_token,
      },
    })
      .then((result) => {
        if (result.data.status) {
          props.setIsProcessing(false);
          setIsProcessing(false);
          Swal.fire("Transaction Completed!", result.data.message, "success");
          user_context.getUserDetails()
          props.toggle();
        } else {
          props.setIsProcessing(false);
          setIsProcessing(false);
          Swal.fire("Transaction Failed!", result.data.message, "error");
        }
      })
      .catch((e) => {
        props.setIsProcessing(false);
        setIsProcessing(false);

        Swal.fire("Transaction Failed!", "error");
      });
  };

  const handleEdit = () => {
    setIsEdit(!isEdit);
    if (isDisabled) {
      setIsDisabled(null);
    } else {
      setIsDisabled(true);
    }
  };

  useEffect(() => {
    let profile = user_context.userData;

    setFirstName(profile.first_name);
    setLastName(profile.last_name);
    setAge(profile.age);
    setGender(profile.gender);
    setMobileNumber(profile.mobile);
    setEmail(profile.email);
    setZipcode(profile.address.zipcode);
    setSpecificAddress(profile.address.specific_address);

    return () => {};
  }, []);

  return (
    <Container fluid className="pt-2">
      <FormControlLabel
        className="m-0"
        control={
          <Switch checked={isEdit} onChange={handleEdit} color="primary" />
        }
        label="Edit"
      />
      <Form onSubmit={(e) => saveProfileHandler(e)} autoComplete="off">
        <Row>
          <Col xs={12}>
            <Row>
              <Col xs={12}>
                <Card className="ml-2 mr-2">
                  <CardBody className="pt-1">
                    <Row>
                      <Col md={6}>
                        <div>
                          <TextField
                            name="first_name"
                            label="First Name"
                            value={firstName}
                            onChange={(e) => setFirstName(e.target.value)}
                            fullWidth
                            required
                            disabled={isDisabled}
                          />
                        </div>
                        <div>
                          <TextField
                            name="last_name"
                            label="Last Name"
                            value={lastName}
                            onChange={(e) => setLastName(e.target.value)}
                            fullWidth
                            required
                            disabled={isDisabled}
                          />
                        </div>
                        <div>
                          <TextField
                            type="number"
                            name="age"
                            label="Age"
                            value={age}
                            onChange={(e) => setAge(e.target.value)}
                            fullWidth
                            required
                            disabled={isDisabled}
                          />
                        </div>
                        <div>
                          <GenderSelect
                            gender={gender}
                            setGender={setGender}
                            disabled={isDisabled}
                          />
                        </div>
                      </Col>
                      <Col md={6}>
                        <div>
                          <TextField
                            name="mobile"
                            type="number"
                            label="Mobile Number"
                            value={mobileNumber || ""}
                            onChange={(e) => setMobileNumber(e.target.value)}
                            fullWidth
                            required
                            disabled={isDisabled}
                          />
                        </div>
                        <div>
                          <TextField
                            type="email"
                            name="email"
                            label="Email Address"
                            onChange={(e) => setEmail(e.target.value)}
                            value={email}
                            fullWidth
                            disabled={isDisabled}
                          />
                        </div>
                        <div>
                          <TextField
                            name="zipcode"
                            label="Zipcode"
                            value={zipcode || ""}
                            onChange={(e) => setZipcode(e.target.value)}
                            fullWidth
                            required
                            disabled={isDisabled}
                          />
                        </div>
                        <div>
                          <TextField
                            label="Complete Address"
                            name="complete_address"
                            value={specificAddress}
                            onChange={(e) => setSpecificAddress(e.target.value)}
                            fullWidth
                            required
                            disabled={isDisabled}
                          />
                        </div>
                      </Col>
                    </Row>
                  </CardBody>
                </Card>
              </Col>
            </Row>
          </Col>
        </Row>
        {!isProcessing && (
          <div>
            {isEdit && (
              <Row className="mt-3 ml-2">
                <Button
                  type="submit"
                  disabled={isDisabled}
                  style={{ minWidth: "120px" }}
                  className="bg-primary text-white"
                >
                  <FontAwesomeIcon icon={faSave} className="mr-1" /> Save
                </Button>
              </Row>
            )}
          </div>
        )}
      </Form>
    </Container>
  );
};

export default PassengerForm;
