import React, { useState } from "react";
import DateFnsUtils from "@date-io/date-fns";
import { Modal, ModalHeader, ModalBody, Row, Col } from "reactstrap";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faClock,
} from "@fortawesome/free-solid-svg-icons";
import LinearProgressBarWithLabel from "../Common/LinearProgressBarWithLabel";
import { useTheme } from "../../../context/theme";
import { useTravel } from "../../../context/travel";
import DirectionsBoatIcon from "@material-ui/icons/DirectionsBoat";
import { Typography } from "@material-ui/core";
import UpdatePreBoardForm from "../Forms/UpdatePreboardForm";

const Preboard = (props) => {
  const dateFns = new DateFnsUtils();
  const theme_context = useTheme();
  const travel_context = useTravel();
  const [isProcessing, setIsProcessing] = useState(false);
  const departureDate = dateFns.format(
    new Date(travel_context.selectedTravel.fromDate),
    "MMMM d, yyyy hh:ii:ss a"
  );


  return (
    <div>
      <Modal
        backdrop={"static"}
        isOpen={props.isOpen}
        toggle={props.toggle}
        className="modal-xl"
        style={{ marginTop: "6%", marginLeft: theme_context.modalMarginLG }}
      >
        <ModalHeader style={{ lineHeight: "2" }} toggle={props.toggle}>
          <DirectionsBoatIcon /> {travel_context.selectedTravel.ships.name}{" "}
           Pre-boarding Details
        </ModalHeader>
        <ModalBody className="pt-0">
          <Row>
            <Typography variant="h6" className="mr-1 ml-4 mt-2">
              <FontAwesomeIcon icon={faClock} className="mr-1" />
              Schedule: {departureDate}
            </Typography>
            <UpdatePreBoardForm
              setIsProcessing={setIsProcessing}
              toggle={props.toggle}
            />
          </Row>
          <div>
            {isProcessing && (
              <Row className="p-2">
                <Col className="text-center">
                  <LinearProgressBarWithLabel title="Updating Pre-boarding Details" />
                </Col>
              </Row>
            )}
          </div>
        </ModalBody>
      </Modal>
    </div>
  );
};

export default Preboard;
