import React, { useCallback, useEffect, useState } from "react";
import { Switch, Route } from "react-router-dom";
import { useAuth } from "../../context/auth";
import { ThemeContext } from "../../context/theme";
import { UserContext } from "../../context/user";
import { CarrierContext } from "../../context/carrier";
import Icon from "../../assets/icon.png";
import API from "../../utils/Api";
import Schedules from "./Schedules/Schedules";
import Travels from "./Travels/Travels";
import Footer from "./Footer/Footer";
import ChangePasswordModal from "../UI/Modals/ChangePassword";
import UpdatePassengerProfile from "../UI/Modals/UpdatePassengerProfile";
import DirectionsBoatIcon from "@material-ui/icons/DirectionsBoat";

import {
  Drawer,
  AppBar,
  Toolbar,
  List,
  Typography,
  IconButton,
  ListItem,
  ListItemIcon,
  ListItemText,
} from "@material-ui/core";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faKey } from "@fortawesome/free-solid-svg-icons";
import MenuIcon from "@material-ui/icons/Menu";
import AccountCircleIcon from "@material-ui/icons/AccountCircle";
import HomeIcon from "@material-ui/icons/Home";
import ExitToAppIcon from "@material-ui/icons/ExitToApp";
import AccountBoxIcon from "@material-ui/icons/AccountBox";
import clsx from "clsx";
import { makeStyles } from "@material-ui/core/styles";
import "./dashboard.css";
import { ViewContext } from "../../context/view";

const drawerWidth = 220;

const useStyles = makeStyles((theme) => ({
  root: {
    display: "flex",
    justifyContent: "center"
  },
  appBar: {
    transition: theme.transitions.create(["margin", "width"], {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),
    backgroundColor: "#1a1751",
  },
  appBarShift: {
    width: `calc(100% - ${drawerWidth}px)`,
    marginLeft: drawerWidth,
    transition: theme.transitions.create(["margin", "width"], {
      easing: theme.transitions.easing.easeOut,
      duration: theme.transitions.duration.enteringScreen,
    }),
  },
  menuButton: {
    marginRight: theme.spacing(2),
  },
  hide: {
    display: "none",
  },
  drawer: {
    width: drawerWidth,
    flexShrink: 0,
  },
  drawerPaper: {
    width: drawerWidth,
    boxShadow: "1px 0px 10px #000",
  },
  drawerHeader: {
    display: "flex",
    // height: '170px',
    alignItems: "center",
    // necessary for content to be below app bar
    ...theme.mixins.toolbar,
    // justifyContent: 'flex-end',
    backgroundColor: "#FFFFFF",
    color: "#FFFFFF",
    margin: "0px",
    padding: "0px",
  },
  content: {
    flexGrow: 1,
    padding: theme.spacing(4),
    transition: theme.transitions.create("margin", {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),
    marginLeft: -drawerWidth,
  },
  contentShift: {
    transition: theme.transitions.create("margin", {
      easing: theme.transitions.easing.easeOut,
      duration: theme.transitions.duration.enteringScreen,
    }),
    marginLeft: 0,
  },
  large: {
    width: theme.spacing(10),
    height: theme.spacing(10),
  },
  nested: {
    paddingLeft: theme.spacing(4),
  },
}));

export default function Dashboard(props) {
  const auth_context = useAuth();
  const token = auth_context.authTokens;
  const classes = useStyles();
  const [view, setView] = useState("carrier");
  const [userData, setUserdata] = useState("");
  const [defaultList, setDefaultList] = useState([]);
  const [carriersList, setCarriersList] = useState([]);
  const [carrierName, setCarrierName] = useState("");
  const [selectedCarrier, setSelectedCarrier] = useState("");
  const [isLoadingCarriers, setIsLoadingCarriers] = useState(true);
  const [drawerStatus, setDrawerStatus] = useState(true);
  const [modalMarginLG, setModalMarginLG] = useState("20%");
  const [modalMargin, setModalMargin] = useState("30%");

  const [changePasswordModal, setVisiblechangePasswordModal] = useState(false);
  const toggleChangePasswordModal = () =>
    setVisiblechangePasswordModal(!changePasswordModal);
  const [updateProfileModal, setUpdateProfileModal] = useState(false);
  const toggleUpdateProfileModal = () =>
    setUpdateProfileModal(!updateProfileModal);

  const handleDrawer = () => {
    setDrawerStatus(!drawerStatus);
    if (drawerStatus) {
      setModalMarginLG("13%");
      setModalMargin("20%");
    } else {
      setModalMarginLG("20%");
      setModalMargin("30%");
    }
  };

  const handleNavigation = (stringPath = "/") => {
    switch (stringPath) {
      case "/":
        props.history.push("/");
        break;
      case "travels":
        props.history.push("/travels");
        setView("carrier");
        break;
      default:
        break;
    }
  };

  const handleLogout = () => {
    sessionStorage.removeItem("passengertokens");
    window.location.reload();
  };

  const changePasswordHandler = (e) => {
    e.preventDefault();
    toggleChangePasswordModal();
  };

  const getUserDetails = () => {
    API.get("auth/profile/me", {
      headers: {
        Authorization: "bearer" + token.access_token,
      },
    }).then((result) => {
      setUserdata(result.data);
    });
  };

  const getCompanies = useCallback(() => {
    API.get("retrieve/carriers", {
      headers: {
        Authorization: "bearer" + token.access_token,
      },
    }).then((result) => {
      setCarriersList(result.data);
      setDefaultList(result.data);
      setIsLoadingCarriers(false);
    });
  }, []);

  useEffect(() => {
    getUserDetails();
    getCompanies();
    return () => {};
  }, []);

  return (
    <ThemeContext.Provider value={{ modalMargin, modalMarginLG }}>
      <ViewContext.Provider value={{ view, setView }}>
        <UserContext.Provider value={{ userData, setUserdata, getUserDetails }}>
          <CarrierContext.Provider
            value={{
              isLoadingCarriers,
              setIsLoadingCarriers,
              carriersList,
              setCarriersList,
              defaultList,
              selectedCarrier,
              setSelectedCarrier,
              carrierName,
              setCarrierName,
            }}
          >
            <div className={classes.root}>
              <AppBar
                position="fixed"
                className={clsx(classes.appBar, {
                  [classes.appBarShift]: drawerStatus,
                })}
              >
                <Toolbar>
                  <IconButton
                    color="inherit"
                    aria-label="open drawer"
                    onClick={handleDrawer}
                    edge="start"
                    className={clsx(classes.menuButton)}
                    style={{ outline: "none" }}
                  >
                    <MenuIcon />
                  </IconButton>
                  <Typography variant="h6" noWrap>
                    <div className="float-left">
                      <img src={Icon} width="33" height="35" alt="EMS Logo" />
                    </div>
                    <div className="float-left">
                      <ul
                        className="p-0 m-0 mt-2 ml-1"
                        style={{ listStyle: "none", lineHeight: "0.9rem" }}
                      >
                        <li style={{ fontSize: "1.5rem" }}>PEMS</li>
                        <li>
                          <small style={{ fontSize: "0.8rem" }}>
                            Passenger e-Manifest System
                          </small>
                        </li>
                      </ul>
                    </div>
                  </Typography>
                </Toolbar>
              </AppBar>
              <Drawer
                className={classes.drawer}
                variant="persistent"
                anchor="left"
                open={drawerStatus}
                classes={{
                  paper: classes.drawerPaper,
                }}
              >
                <div className={classes.drawerHeader}>
                  <div
                    className="container-fluid text-left mb-2"
                    style={{
                      color: "#1a1751",
                      minHeight: "125px",
                      height: "125px",
                    }}
                  >
                    <div className="text-center mt-4 pb-2 border-bottom">
                      <AccountCircleIcon
                        style={{ width: "45px", height: "50px" }}
                      />
                      <ul
                        className="m-0 p-0 mt-2"
                        style={{ listStyle: "none", lineHeight: "1em" }}
                      >
                        <li className="border p-1 bg-primary text-white">
                          <strong>{token.user.toUpperCase()}</strong>
                        </li>
                        <li className="mt-2">Passenger</li>
                      </ul>
                    </div>
                  </div>
                </div>
                <List style={{ display: "flex", flexDirection: "column" }}>
                  <ListItem
                    className="mt-4"
                    button
                    onClick={() => handleNavigation("/")}
                  >
                    <ListItemIcon>
                      <HomeIcon />
                    </ListItemIcon>
                    <ListItemText primary={"Dashboard"} />
                  </ListItem>
                  <ListItem button onClick={(e) => changePasswordHandler(e)}>
                    <ListItemIcon>
                      <FontAwesomeIcon icon={faKey} />
                    </ListItemIcon>
                    <ListItemText primary={"My Account"} />
                  </ListItem>
                  <ListItem button onClick={(e) => toggleUpdateProfileModal()}>
                    <ListItemIcon>
                      <AccountBoxIcon />
                    </ListItemIcon>
                    <ListItemText primary={"My Profile"} />
                  </ListItem>
                  <ListItem button onClick={() => handleNavigation("travels")}>
                    <ListItemIcon>
                      <DirectionsBoatIcon />
                    </ListItemIcon>
                    <ListItemText primary={"My Travels"} />
                  </ListItem>
                  <ListItem button onClick={() => handleLogout()}>
                    <ListItemIcon>
                      <ExitToAppIcon />
                    </ListItemIcon>
                    <ListItemText primary={"Logout"} />
                  </ListItem>
                  <Footer />
                </List>
              </Drawer>
              <main
                className={clsx(classes.content, {
                  [classes.contentShift]: drawerStatus,
                })}
              >
                <Switch>
                  <Route exact path="/">
                    <Schedules />
                  </Route>
                  <Route exact path="/travels">
                    <Travels />
                  </Route>
                </Switch>

                {changePasswordModal && (
                  <ChangePasswordModal
                    isOpen={changePasswordModal}
                    toggle={toggleChangePasswordModal}
                  />
                )}

                {updateProfileModal && (
                  <UpdatePassengerProfile
                    isOpen={updateProfileModal}
                    toggle={toggleUpdateProfileModal}
                  />
                )}
              </main>
            </div>
          </CarrierContext.Provider>
        </UserContext.Provider>
      </ViewContext.Provider>
    </ThemeContext.Provider>
  );
}
