import React, { useState } from "react";

import {
  MuiPickersUtilsProvider,
  KeyboardDatePicker,
} from "@material-ui/pickers";
import DateFnsUtils from "@date-io/date-fns";

const FilterDate = (props) => {
  const [filterDate, setFilterDate] = useState(null);

  const handleChange = (d) => {
    setFilterDate(d);
    if(props.filterDate){
        props.filterDate(d);
    }
  };

  return (
    <div className="mb-2">
      <MuiPickersUtilsProvider utils={DateFnsUtils}>
        <KeyboardDatePicker
          value={filterDate}
          onChange={(d, v) => handleChange(d)}
          label="Filter Dates"
          format="MMMM d, yyyy"
          required
          disablePast
          clearable
          hideTabs
          showTodayButton
          fullWidth
          helperText=""
          error={false}
        />
      </MuiPickersUtilsProvider>
    </div>
  );
};

export default FilterDate;
