import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import Card from "@material-ui/core/Card";
import CardActionArea from "@material-ui/core/CardActionArea";
import CardActions from "@material-ui/core/CardActions";
import CardContent from "@material-ui/core/CardContent";
import CardMedia from "@material-ui/core/CardMedia";
import Button from "@material-ui/core/Button";
import Typography from "@material-ui/core/Typography";
import ShipImage from "../../../assets/img_card.jpg";
import CustomTooltip from "./CustomTooltip";
import DirectionsBoatIcon from "@material-ui/icons/DirectionsBoat";
import TodayIcon from "@material-ui/icons/Today";
import { Row, Col } from "reactstrap";

const useStyles = makeStyles({
  root: {
    maxWidth: 250,
    minWidth: 250,
    maxHeight: 220,
    margin: 10,
  },
});

export default function ShipCards(props) {
  const classes = useStyles();

  return (
    <Card className={classes.root}>
      <CardActionArea>
        <CardMedia
          component="img"
          alt={props.carrier_name.toUpperCase()}
          height="100"
          image={ShipImage}
          title={props.carrier_name.toUpperCase()}
        />
        <CardContent className="m-0 p-1 bg-primary text-white">
          <CustomTooltip content={props.carrier_name.toUpperCase()}>
            <Typography noWrap variant="h6" component="h5">
              {props.carrier_name.toUpperCase()}
            </Typography>
          </CustomTooltip>
        </CardContent>
      </CardActionArea>
      <CardActions className="mb-1 p-0 d-block">
        <Row className="m-0">
          <Col md={12} className="p-0">
            <Button
              className="btnLeftContent p-2 pt-1 pl-3"
              style={{ minWidth: "100%" }}
              size="small"
              color="primary"
              onClick={() => props.handleSelectedCarrier(props.carrier)}
            >
              <DirectionsBoatIcon className="mr-1" fontSize="small" /> Ships
            </Button>
          </Col>
          <Col md={12} className="p-0">
            <Button
              className="btnLeftContent p-2 pt-1 pl-3"
              style={{ minWidth: "100%" }}
              size="small"
              color="primary"
              onClick={() => props.handleCarrierSchedules(props.carrier)}
            >
              <TodayIcon className="mr-1" fontSize="small" /> Schedules
            </Button>
          </Col>
        </Row>
      </CardActions>
    </Card>
  );
}
