import React, { useEffect, useState } from "react";
import API from "../../../utils/Api";
import DateFnsUtils from "@date-io/date-fns";
import { Card, Paper, Button, Typography } from "@material-ui/core";
import { Container, Row, Col, CardBody } from "reactstrap";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faArrowLeft } from "@fortawesome/free-solid-svg-icons";
import CarriersGrid from "../../UI/Grid/CarriersGrid";
import { ScheduleContext } from "../../../context/schedule";
import { useAuth } from "../../../context/auth";
import { useView } from "../../../context/view";
import { useCarrier } from "../../../context/carrier";
import { ShipsContext } from "../../../context/ships";
import DirectionsBoatIcon from "@material-ui/icons/DirectionsBoat";
import CarriersSearchBar from "../../UI/Common/CarriersSearchBar";
import ShipsGrid from "../../UI/Grid/ShipsGrid";
import ShipsSearchBar from "../../UI/Common/ShipsSearchBar";
import SchedulesGrid from "../../UI/Grid/SchedulesGrid";
import ShipSchedulesGrid from "../../UI/Grid/ShipSchedulesGrid";
import FilterDate from "../../UI/Forms/Fields/FilterDate";
import Preboard from "../../UI/Modals/Preboard";

const Schedules = (props) => {
  const dateFns = new DateFnsUtils();
  const carriers_context = useCarrier();
  const view_context = useView();
  const token = useAuth().authTokens;
  const [isShipsLoading, setIsShipsLoading] = useState(true);
  const [isScheduleLoading, setIsScheduleLoading] = useState(true);
  const [shipsList, setShipsList] = useState([]);
  const [defaultShips, setDefaultShips] = useState([]);
  const [selectedShip, setSelectedShip] = useState("");
  const [carrierScheduleList, setCarrierScheduleList] = useState([]);
  const [defaultScheduleList, setDefaultScheduleList] = useState([]);
  const [shipScheduleList, setShipScheduleList] = useState([]);
  const [defaultShipScheduleList, setDefaultShipScheduleList] = useState([]);
  const [selectedSchedule, setSelectedSchedule] = useState("");
  const [isPreboard, setIsPreboard] = useState(false);
  const togglePreboarding = () => setIsPreboard(!isPreboard);

  const handleCarrierScheduleList = (carrier) => {
    view_context.setView("carrier-schedules");
    carriers_context.setCarrierName(carrier.carrier_name);
    setCarrierScheduleList([]);
    setDefaultScheduleList([]);
    setIsScheduleLoading(true);
    API.get(`carrier/transport/voyage/scheduled-carrier/${carrier.id}`, {
      headers: {
        Authorization: "bearer " + token.access_token,
      },
    }).then((result) => {
      setCarrierScheduleList(result.data);
      setDefaultScheduleList(result.data);
      setIsScheduleLoading(false);
    });
  };

  const handleShipScheduleList = (ship) => {
    view_context.setView("ship-schedules");
    setShipScheduleList([]);
    setDefaultShipScheduleList([]);
    setIsScheduleLoading(true);
    API.get(`carrier/transport/voyage/passenger-scheduled-ships/${ship.id}`, {
      headers: {
        Authorization: "bearer " + token.access_token,
      },
    }).then((result) => {
      setShipScheduleList(result.data);
      setDefaultShipScheduleList(result.data);
      setIsScheduleLoading(false);
    });
  };

  const handleFilterCarrierScheduleDate = (d) => {
    let filteredSchedule = defaultScheduleList.filter((schedule) => {
      return dateFns.isSameDay(
        dateFns.date(schedule.departure),
        dateFns.date(d)
      );
    });

    setCarrierScheduleList(filteredSchedule);
    setIsScheduleLoading(false);
  };

  const handleFilterShipScheduleDate = (d) => {
    let filteredSchedule = defaultShipScheduleList.filter((schedule) => {
      return dateFns.isSameDay(
        dateFns.date(schedule.departure),
        dateFns.date(d)
      );
    });

    setShipScheduleList(filteredSchedule);
    setIsScheduleLoading(false);
  };

  const handlePreboarding = (schedule) => {
    setSelectedSchedule(schedule);
    togglePreboarding();
  }; 

  return (
    <ScheduleContext.Provider
      value={{
        isScheduleLoading,
        setIsScheduleLoading,
        carrierScheduleList,
        setCarrierScheduleList,
        handleCarrierScheduleList,
        shipScheduleList,
        setShipScheduleList,
        handleShipScheduleList,
        selectedSchedule,
        setSelectedSchedule,
        handlePreboarding,
      }}
    >
      <ShipsContext.Provider
        value={{
          isShipsLoading,
          setIsShipsLoading,
          shipsList,
          setShipsList,
          defaultShips,
          setDefaultShips,
          selectedShip,
          setSelectedShip,
        }}
      >
        <Container fluid="true" className="mt-5 p-0 m-0 d-block">
          <Row className="m-0 p-0">
            {view_context.view === "carrier" && (
              <Col md={12} className="pl-0 ml-0 pr-0 mr-0">
                <Card>
                  <CardBody>
                    <Row>
                      <Col md={6}>
                        <h4 className="text-primary mt-2">
                          <DirectionsBoatIcon /> Shipping Lines
                        </h4>
                      </Col>
                      <Col md={6} className="text-right">
                        <CarriersSearchBar />
                      </Col>
                      <Col md={12}>
                        <div className="p-2 text-center">
                          <CarriersGrid />
                        </div>
                      </Col>
                    </Row>
                  </CardBody>
                </Card>
              </Col>
            )}
            {view_context.view === "ships" && (
              <Col md={12} className="pl-0 ml-0 pr-0 mr-0">
                <Card>
                  <CardBody>
                    <Row>
                      <Col md={6}>
                        <Typography variant="h5" className="ml-2">
                          <Button
                            onClick={() => view_context.setView("carrier")}
                          >
                            <FontAwesomeIcon
                              icon={faArrowLeft}
                              className="mr-1"
                            />{" "}
                            Back
                          </Button>
                          <DirectionsBoatIcon />{" "}
                          {carriers_context.carrierName.toUpperCase()}
                        </Typography>
                      </Col>
                      <Col md={6} className="text-right">
                        <ShipsSearchBar />
                      </Col>
                      <Col md={12}>
                        <div className="p-2 text-center">
                          <ShipsGrid />
                        </div>
                      </Col>
                    </Row>
                  </CardBody>
                </Card>
              </Col>
            )}
            {view_context.view === "carrier-schedules" && (
              <Col md={12} className="pl-0 ml-0 pr-0 mr-0">
                <Card>
                  <CardBody>
                    <Row>
                      <Col md={8}>
                        <Typography variant="h5" className="ml-2">
                          <Button
                            onClick={() => view_context.setView("carrier")}
                          >
                            <FontAwesomeIcon
                              icon={faArrowLeft}
                              className="mr-1"
                            />{" "}
                            Back
                          </Button>
                          <DirectionsBoatIcon />{" "}
                          {carriers_context.carrierName.toUpperCase()}
                        </Typography>
                      </Col>
                      <Col md={4}>
                        <FilterDate
                          filterDate={(d) => handleFilterCarrierScheduleDate(d)}
                        />
                      </Col>
                      <Col md={12}>
                        <div className="p-2 text-center">
                          <SchedulesGrid />
                        </div>
                      </Col>
                    </Row>
                  </CardBody>
                </Card>
              </Col>
            )}
            {view_context.view === "ship-schedules" && (
              <Col md={12} className="pl-0 ml-0 pr-0 mr-0">
                <Card>
                  <CardBody>
                    <Row>
                      <Col md={8}>
                        <Typography variant="h5" className="ml-2">
                          <Button onClick={() => view_context.setView("ships")}>
                            <FontAwesomeIcon
                              icon={faArrowLeft}
                              className="mr-1"
                            />{" "}
                            Back
                          </Button>
                          <DirectionsBoatIcon />{" "}
                          {carriers_context.carrierName.toUpperCase()}
                        </Typography>
                      </Col>
                      <Col md={4}>
                        <FilterDate
                          filterDate={(d) => handleFilterShipScheduleDate(d)}
                        />
                      </Col>
                      <Col md={12}>
                        <Paper className="p-2 text-center">
                          <ShipSchedulesGrid />
                        </Paper>
                      </Col>
                    </Row>
                  </CardBody>
                </Card>
              </Col>
            )}
          </Row>
        </Container>
        {isPreboard && (
          <Preboard toggle={togglePreboarding} isOpen={isPreboard} />
        )}
      </ShipsContext.Provider>
    </ScheduleContext.Provider>
  );
};

export default Schedules;
