import React, { useState, useEffect } from "react";
import API from "../../../utils/Api";
import DateFnsUtils from "@date-io/date-fns";
import { Container, Row, Col, Card, CardBody, Button } from "reactstrap";
import { useCarrier } from "../../../context/carrier";
import { useAuth } from "../../../context/auth";
import { useView } from "../../../context/view";
import { TravelContext } from "../../../context/travel"; 
import Typography from "@material-ui/core/Typography";
import Tooltip from "@material-ui/core/Tooltip";
import DataTable from "../../UI/Datatable/Datatable";
import DirectionsBoatIcon from "@material-ui/icons/DirectionsBoat";
import LinearProgressBarWithLabel from "../../UI/Common/LinearProgressBarWithLabel";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faBan, faUserEdit } from "@fortawesome/free-solid-svg-icons";
import { isArray } from "lodash";
import Swal from "sweetalert2";
import UpdatePreBoard from "../../UI/Modals/UpdatePreboard";

const Travels = (props) => {
  const dateFns = new DateFnsUtils();
  const token = useAuth().authTokens;
  const [isLoading, setIsLoading] = useState(true);
  const [travelDataList, setTravelDataList] = useState([]);
  const [selectedTravel, setSelectedTravel] = useState([]);

  const [updatePreboarding, setUpdatePreboarding] = useState(false);
  const toggleUpdatePreboarding = () =>
    setUpdatePreboarding(!updatePreboarding);

  const formatData = (data) => {
    if (!isArray(data)) {
      return [];
    }

    if (data.length === 0) {
      return [];
    }

    if (data !== null && data.length > 0) {
      return data.map((p, index) => {
        let rs = [
          dateFns.format(new Date(p.fromDate), "MMMM d, yyyy"),
          dateFns.format(new Date(p.fromDate), "hh:mm:ss a"),
          p.ships[0].name,
          p.fromLocation,
          p.toLocation,
          <Typography
            variant="button"
            className={
              p.status.toLowerCase() === "pending"
                ? "text-danger"
                : "text-success"
            }
          >
            {p.status.toLowerCase() === "pending" ? "PRE-BOARDED" : "BOARDED"}
          </Typography>,
          [p, p.status.toLowerCase(), data, index],
        ];

        return rs;
      });
    }
  };

  const getTravelHistory = () => {
    API.get(`auth/profile/history`, {
      headers: {
        Authorization: "bearer " + token.access_token,
      },
    })
      .then((result) => {
        if (result.status === 200) {
          let data = result.data;
          setTravelDataList(formatData(data));
          setIsLoading(false);
        }
      })
      .catch((e) => {
        setIsLoading(false);
      });
  };

  const handleUpdatePreboarding = (travel) => {
    setSelectedTravel(travel[0]);
    toggleUpdatePreboarding();
  };

  const archiveHandler = (dataParams) => {
    let data = dataParams;

    Swal.fire({
      title: "Cancel Pre-boarding?",
      text: "You won't be able to revert this!",
      icon: "warning",
      showCancelButton: true,
      confirmButtonColor: "#3085d6",
      cancelButtonColor: "#d33",
      confirmButtonText: "Yes",
    }).then((result) => {
      if (result.isConfirmed) {
        API.delete(`auth/profile/delete/${data[0].id}`, {
          headers: {
            Authorization: "bearer " + token.access_token,
          },
        }).then((result) => {
          if (result.data.status) {
            Swal.fire("Pre-boarding Cancelled", result.data.message, "success");
            data[2].splice(data[3], 1);
            let newList = formatData(data[2]);
            setTravelDataList(newList);
          } else {
            Swal.fire("Transaction Failed", result.data.message, "error");
          }
        });
      }
    });
  };

  const columns = [
    "Departure Date",
    "Time",
    "Ship Name",
    "Origin",
    "Destination",
    "Status",
    {
      name: "Actions",
      options: {
        customBodyRender: (value, tableMeta, updateValue) => {
          if (value[1] === "pending") {
            return (
              <div style={{ color: "#1a1751", justifyContent: "center" }}>
                <Tooltip title="View Details" placement="top">
                  <Button
                    color="link"
                    className="p-1 m-0 btn text-primary"
                    onClick={() => handleUpdatePreboarding(value)}
                  >
                    <FontAwesomeIcon icon={faUserEdit} />
                  </Button>
                </Tooltip>
                <Tooltip title="Cancel Pre-boarding" placement="top">
                  <Button
                    color="link"
                    className="p-1 m-0 btn text-danger"
                    onClick={() => archiveHandler(value)}
                  >
                    <FontAwesomeIcon icon={faBan} />
                  </Button>
                </Tooltip>
              </div>
            );
          } else {
            return (
              <Typography variant="button" className="text-success">
                Completed
              </Typography>
            );
          }
        },
      },
    },
  ];

  const options = {
    filterType: "dropdown",
    tableBodyHeight: "350px",
    responsive: "standard",
    selectableRows: "none",
    print: false,
    download: false,
    viewColumns: false,
    textLabels: {
      body: {
        noMatch: "No records found",
      },
    },
  };

  useEffect(() => {
    getTravelHistory();
    return () => {};
  }, []);

  return (
    <TravelContext.Provider value={{ selectedTravel, setSelectedTravel, getTravelHistory }}>
      <Container fluid="true" className="mt-5 p-0 m-0">
        <Row className="m-0 p-0">
          <Col md={12} className="pl-0 ml-0 pr-0 mr-0">
            <Card>
              <CardBody>
                <Typography variant="h6" className="text-primary">
                  <DirectionsBoatIcon /> Travels
                </Typography>
                {isLoading && (
                  <div className="mt-3">
                    <LinearProgressBarWithLabel title="Loading Travels..." />
                  </div>
                )}
                {!isLoading && travelDataList.length === 0 && (
                  <div className="text-center" style={{ color: "#ccc" }}>
                    <Typography variant="h5">No Travel Data Found</Typography>
                  </div>
                )}
                {!isLoading && travelDataList.length > 0 && (
                  <div>
                    <DataTable
                      data={travelDataList}
                      isLoading={isLoading}
                      columns={columns}
                      options={options}
                    />
                  </div>
                )}
              </CardBody>
            </Card>
          </Col>
        </Row>
        {updatePreboarding && (
          <UpdatePreBoard
            isOpen={updatePreboarding}
            toggle={toggleUpdatePreboarding}
          />
        )}
      </Container>
    </TravelContext.Provider>
  );
};

export default Travels;
