import React from "react";
import DateFnsUtils from "@date-io/date-fns";
import { makeStyles } from "@material-ui/core/styles";
import { useSchedule } from "../../../context/schedule";
import Card from "@material-ui/core/Card";
import CardActionArea from "@material-ui/core/CardActionArea";
import CardActions from "@material-ui/core/CardActions";
import CardContent from "@material-ui/core/CardContent";
import Button from "@material-ui/core/Button";
import Typography from "@material-ui/core/Typography";
import TodayIcon from "@material-ui/icons/Today";
import { Paper } from "@material-ui/core";
import { faAnchor, faInfoCircle } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import "./card.css";

const useStyles = makeStyles({
  root: {
    minWidth: 250,
    maxWidth: 200,
    maxHeight: 210,
    margin: 15,
    marginBottom: 0,
  },
});

export default function ShipCards(props) {
  const classes = useStyles();
  const dateFns = new DateFnsUtils();
  const time = dateFns.format(new Date(props.schedule.departure), "h:mm a");
  const month = dateFns.format(new Date(props.schedule.departure), "MMMM d");
  const schedule_context = useSchedule();

  return (
    <Card className={classes.root}>
      <CardActionArea>
        <Paper elevation={0} style={{ minHeight: 50 }}>
          <Typography
            className="bgMonth text-white p-3"
            noWrap
            variant="h4"
            component="h4"
            fontWeight="bold"
          >
            {month}
          </Typography>
        </Paper>
        <CardContent className="m-1 p-1">
          <Typography noWrap variant="h5" component="h5" fontWeight="bold">
            {time}
          </Typography>
          <Typography noWrap fontWeight="bold" fontSize="small">
            <FontAwesomeIcon icon={faAnchor} className="mr-1" /> {props.schedule.ship.name}
          </Typography>
          <Typography fontWeight="bold" fontSize="small">
            {props.schedule.ship.routes.toUpperCase()}
          </Typography>
        </CardContent>
      </CardActionArea>
      <CardActions
        className="text-center m-0 p-0"
        style={{ justifyContent: "center" }}
      >
        {dateFns.isBefore(Date.now(), new Date(props.schedule.departure)) && (
          <Button
            style={{ minWidth: "100%", width: "100%" }}
            size="small"
            color="primary"
            onClick={() => schedule_context.handlePreboarding(props.schedule)}
            fontSize="35px"
          >
            <TodayIcon className="mr-1" fontSize="small" /> PRE-BOARD
          </Button>
        )}

        {dateFns.isAfter(Date.now(), new Date(props.schedule.departure)) && (
          <div className="text-success">
            <FontAwesomeIcon icon={faInfoCircle} /> Ship Departed
          </div>
        )}
      </CardActions>
    </Card>
  );
}
