import React, { useEffect } from "react";
import { makeStyles } from "@material-ui/core/styles";
import { Row } from "reactstrap";
import "./grid.css";
import API from "../../../utils/Api";
import { useAuth } from "../../../context/auth";
import { useView } from "../../../context/view";
import { useCarrier } from "../../../context/carrier";
import { useSchedule } from "../../../context/schedule";
import { useShips } from "../../../context/ships";
import CarrierCards from "../Common/CarrierCard";
import LinearProgressBarWithLabel from "../Common/LinearProgressBarWithLabel";
import { Typography } from "@material-ui/core";

const useStyles = makeStyles((theme) => ({
  root: {
    display: "flex",
    flexWrap: "wrap",
    overflow: "hidden",
    backgroundColor: theme.palette.background.paper,
  },
}));

export default function CarriersGrid(props) {
  const token = useAuth().authTokens;
  const view_context = useView();
  const carriers_context = useCarrier();
  const schedule_context = useSchedule();
  const ships_context = useShips();
  const classes = useStyles();

  const handleSelectedCarrier = (carrier) => {
    view_context.setView("ships");
    carriers_context.setSelectedCarrier(carrier);
    carriers_context.setCarrierName(carrier.carrier_name);
    ships_context.setShipsList([]);
    ships_context.setDefaultShips([]);
    API.get(`carrier/ships/${carrier.id}`, {
      headers: {
        Authorization: "bearer " + token.access_token,
      },
    }).then((result) => {
      ships_context.setShipsList(result.data);
      ships_context.setDefaultShips(result.data);
      ships_context.setIsShipsLoading(false);
    });
  };

  useEffect(() => {
    ships_context.setIsShipsLoading(true);
    return () => {};
  }, []);

  return (
    <div className={classes.root}>
      <Row
        className="ml-1"
        style={{
          minWidth: "100%",
          overflowY: "hidden",
          overflowX: "hidden",
          justifyContent:
            carriers_context.carriersList.length === 0
              ? "center"
              : "flex-start",
        }}
      >
        {carriers_context.carriersList.length > 0 && (
          <Row
            style={{
              minHeight: "450px",
              minWidth: "100%",
              marginLeft: "35px",
              width: "100%",
              maxHeight: "450px",
              paddingRight: "30px",
              boxSizing: "content-box",
              overflowY: "scroll",
            }}
          >
            {carriers_context.carriersList.map((carrier) => (
              <CarrierCards
                carrier_name={carrier.carrier_name}
                key={carrier.id}
                carrier={carrier}
                handleSelectedCarrier={(carrier) =>
                  handleSelectedCarrier(carrier)
                }
                handleCarrierSchedules={(carrier) =>
                  schedule_context.handleCarrierScheduleList(carrier)
                }
              />
            ))}
          </Row>
        )}
        {carriers_context.carriersList.length === 0 && (
          <div
            className="text-center col-12"
            style={{ justifyContent: "center", color: "#ccc" }}
          >
            {carriers_context.isLoadingCarriers && (
              <LinearProgressBarWithLabel title="Loading Shipping Lines..." />
            )}

            {!carriers_context.isLoadingCarriers && (
              <Typography variant="h5">No Shipping Lines Found</Typography>
            )}
          </div>
        )}
      </Row>
    </div>
  );
}
