import React, { useState } from "react";
import { Redirect } from "react-router-dom"
import API from "../../utils/Api";
import TextField from "@material-ui/core/TextField";
import Button from "@material-ui/core/Button";
import InputAdornment from "@material-ui/core/InputAdornment";
import IconButton from "@material-ui/core/IconButton";
import Visibility from "@material-ui/icons/Visibility";
import VisibilityOff from "@material-ui/icons/VisibilityOff";
import { Container, Row, Col, Card, Form, Spinner, CardBody } from "reactstrap";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faArrowLeft,
  faArrowRight,
  faCheck,
  faCheckDouble,
  faEnvelope,
  faSignInAlt,
  faUserCheck,
} from "@fortawesome/free-solid-svg-icons";
import "../Register/register.css";
import Logo from "../../assets/logo.jpg";
import MARINA from "../../assets/marina.png";
import Error from "../UI/Error/Error";
import GenderSelect from "../UI/Forms/Fields/GenderSelect";
import Swal from "sweetalert2";
import { useAuth } from "../../context/auth"

const Register = (props) => {
  const { authTokens } = useAuth()
  const [isProcessing, setProcessing] = useState(false);
  const [isError, setIsError] = useState(false);
  const [errorMessage, setErrorMessage] = useState(
    "Oops! sorry something went wrong..."
  );
  const [isShowPass, setIsShowPass] = useState(false);
  const toggleShowPassword = () => setIsShowPass(!isShowPass);
  const [isShowRePass, setIsShowRePass] = useState(false);
  const toggleShowRePassword = () => setIsShowRePass(!isShowRePass);

  const [email, setEmail] = useState("");
  const [code, setCode] = useState("");
  const [username, setUsername] = useState("");
  const [password, setPassword] = useState("");
  const [repassword, setRepassword] = useState("");

  // Personal
  const [firstName, setFirstName] = useState("");
  const [lastName, setLastName] = useState("");
  const [gender, setGender] = useState("");
  const [age, setAge] = useState("");
  // Contact
  const [mobileNumber, setMobileNumber] = useState("");
  // Address
  const [zipcode, setZipcode] = useState("");
  const [specificAddress, setSpecificAddress] = useState("");

  const [step1, setStep1] = useState(true);
  const [step2, setStep2] = useState(false);
  const [step3, setStep3] = useState(false);
  const [step4, setStep4] = useState(false);
  const [isCodeValidated, setIsCodeValidated] = useState(false);

  const isAuthenticatedHandler = () => {
    const token = authTokens;

    if (token === null) {
      return false;
    } else {
      if (token.expires_in < Date.now()) {
        return true;
      }
    }
    return false;
  };

  if (isAuthenticatedHandler()) {
    return <Redirect to="/" />;
  }

  const goto = (step) => {
    setIsError(false);
    setProcessing(false);
    setStep1(false);
    setStep2(false);
    setStep3(false);
    setStep4(false);

    switch (step) {
      case 1:
        setStep1(true);
        break;
      case 2:
        setStep2(true);
        break;
      case 3:
        setStep3(true);
        break;
      case 4:
        setStep4(true);
        break;
      default:
        setStep1(true);
        break;
    }
  };

  const validateEmail = (e) => {
    e.preventDefault();

    if (email !== "") {
      setProcessing(true);

      API.post("profile/email", { email: email })
        .then((result) => {
          setProcessing(false);
          if (result.status === 200) {
            if (result.data.error === 0) {
              setErrorMessage(result.data.message);
              setIsError(true);
            } else {
              setEmail(email);
              goto(2);
            }
          } else if (result.status === 254) {
            setErrorMessage(result.data.message);
            setIsError(true);
          } else {
            setIsError(true);
          }
        })
        .catch((e) => {
          setProcessing(false);
          setIsError(true);
        });
    } else {
      setErrorMessage("Please enter a valid email");
      setIsError(true);
    }
  };

  const validateCode = (e) => {
    e.preventDefault();

    if (code !== "") {
      setProcessing(true);
      API.post("profile/check-code", { _email: email, _code: code })
        .then((result) => {
          setProcessing(false);
          if (result.status === 200) {
            if (result.data.error === 0) {
              setErrorMessage(result.data.message);
              setIsError(true);
            } else {
              setIsCodeValidated(true);
              setCode(code);
              goto(3);
            }
          } else if (result.status === 254) {
            setErrorMessage(result.data.message);
            setIsError(true);
          } else {
            setIsError(true);
          }
        })
        .catch((e) => {
          setProcessing(false);
          setIsError(true);
        });
    } else {
      setErrorMessage("Please enter the valid code");
      setIsError(true);
    }
  };

  const saveProfile = (e) => {
    e.preventDefault();
    goto(4);
  };

  const submitPassengerData = () => {
    if (
      password.replace(/\s+/, "").length <= 0 ||
      password === "" ||
      username === ""
    ) {
      setIsError(true);
      setErrorMessage("Please enter valid username and password");
      return null;
    }

    if (password !== repassword) {
      setIsError(true);
      setErrorMessage("Password doesn't match");
      return null;
    }

    setProcessing(true);
    let postData = {
      username: username,
      password: password,
      email: email,
      first_name: firstName,
      last_name: lastName,
      age: age,
      gender: gender,
      mobile: mobileNumber,
      specific_address: specificAddress,
      zipcode: zipcode,
    };

    API.post("profile/register", postData)
      .then((result) => {
        setProcessing(false);
        if (result.status === 200) {
          if (result.data.error === 0) {
            setErrorMessage(result.data.message);
            setIsError(true);
          } else {
            if (!result.data.status) {
              setErrorMessage(result.data.message);
              setIsError(true);
            } else {
              let res = Swal.fire(
                "Transaction Completed!",
                "Personal Record Registered!",
                "success"
              );

              res.then(() => {
                props.history.push("/");
              });
            }
          }
        } else if (result.status === 254) {
          setErrorMessage(result.data.message);
          setIsError(true);
        } else {
          setIsError(true);
        }
      })
      .catch((e) => {
        setProcessing(false);
        setIsError(true);
      });
  };

  const handleChangeEmail = (value) => {
    setEmail(value);
    setIsError(false);
  };

  const handleChangeCode = (value) => {
    setCode(value);
    setIsError(false);
  };

  return (
    <Container className="rounded" style={{ width: "768px", marginTop: "2%" }}>
      <Card
        className="p-0 m-0 rounded"
        style={{ border: "none", opacity: "0.88" }}
      >
        <Row className="p-0 m-0">
          <Col xs={12} className="p-0 m-0">
            <Card className="p-0 m-0" style={{ borderRadius: "0px" }}>
              <Col xs={12} className="mt-4 text-center">
                <img
                  className="mt-1"
                  src={MARINA}
                  style={{ width: "4rem", height: "4.5rem" }}
                  alt="MARINA Logo"
                />
                <img
                  className="mt-1"
                  src={Logo}
                  style={{ width: "5rem", height: "6rem" }}
                  alt="Passenger eTracer Logo"
                />
              </Col>
              <Col md={12} className="mt-2 text-center mb-1">
                <h4>
                  <FontAwesomeIcon className="pr-1" icon={faUserCheck} />{" "}
                  Passenger Registration
                </h4>
              </Col>
              {isError && (
                <Col xs={12} className="pl-4 pr-4 mb-2 text-center">
                  {isError && <Error>{errorMessage}</Error>}
                </Col>
              )}
              {step1 && (
                <div>
                  <Col xs={12} className="mt-2 text-center mb-2">
                    <h5>STEP 1: Email Validation</h5>
                    <Col
                      md={{ size: 10, offset: 1 }}
                      className="text-justify pl-4 pr-4 mt-4"
                    >
                      <h6>
                        Use your official email address. This email address will
                        receive notifications and account recovery information.
                      </h6>
                    </Col>
                  </Col>
                  <Col xs={{ size: 10, offset: 1 }}>
                    <Form onSubmit={(e) => validateEmail(e)} autoComplete="off">
                      <Col xs={12} className="pl-4 pr-4 mb-2">
                        <TextField
                          type="email"
                          name="email"
                          placeholder="Email"
                          label="Email"
                          value={email || ""}
                          onChange={(e) => handleChangeEmail(e.target.value)}
                          fullWidth
                          required
                        />
                      </Col>
                      <Col xs={12} className="pl-4 pr-4 mt-3 mb-5">
                        {isProcessing ? (
                          <div
                            className="text-center mt-1"
                            style={{ minHeight: "100px" }}
                          >
                            <Spinner
                              className="mt-4"
                              style={{ width: "2rem", height: "2rem" }}
                              color="primary"
                            />
                            <br />
                            <span className="mt-1">Processing...</span>
                          </div>
                        ) : (
                          <div>
                            <Button
                              type="submit"
                              variant="contained"
                              color="primary"
                              className="pt-1"
                              onChange={() => setIsError(false)}
                              disabled={isProcessing}
                              fullWidth
                            >
                              <span>Validate Email</span>
                              <FontAwesomeIcon
                                className="pl-1"
                                icon={faEnvelope}
                              />
                            </Button>
                            <div className="text-center m-0 p-0 mt-2">
                              <span>or</span>
                            </div>
                            <div className="text-center">
                              <a
                                href="login"
                                variant="contained"
                                color="primary"
                                className="pt-1 btn"
                                disabled={isProcessing}
                              >
                                <FontAwesomeIcon
                                  className="pr-1"
                                  icon={faSignInAlt}
                                />
                                <span>LOGIN HERE</span>
                              </a>
                            </div>
                          </div>
                        )}
                      </Col>
                    </Form>
                  </Col>
                </div>
              )}
              {step2 && (
                <div>
                  <Col xs={12} className="mt-2 text-center mb-2">
                    <h5>STEP 2: Code Validation</h5>
                    <Col
                      md={{ size: 10, offset: 1 }}
                      className="text-justify pl-4 pr-4 mt-4"
                    >
                      <h6>
                        We've sent you an email. The system has sent an email
                        validation code to your given email address. Copy the
                        code given and paste it in the input field.
                      </h6>
                    </Col>
                  </Col>
                  <Col xs={{ size: 10, offset: 1 }}>
                    <Form onSubmit={(e) => validateCode(e)} autoComplete="off">
                      <Col xs={12} className="pl-4 pr-4 mb-2">
                        <TextField
                          type="text"
                          name="code"
                          value={code || ""}
                          onChange={(e) => handleChangeCode(e.target.value)}
                          placeholder="Code"
                          label="Code"
                          fullWidth
                          required
                        />
                      </Col>
                      <Col xs={12} className="mt-4 mb-3">
                        {isProcessing ? (
                          <div
                            className="text-center mt-1"
                            style={{ minHeight: "100px" }}
                          >
                            <Spinner
                              className="mt-4"
                              style={{ width: "2rem", height: "2rem" }}
                              color="primary"
                            />
                            <br />
                            <span className="mt-1">Processing...</span>
                          </div>
                        ) : (
                          <div>
                            <Button
                              type="submit"
                              variant="contained"
                              color="primary"
                              className="pt-1"
                              disabled={isProcessing}
                              fullWidth
                            >
                              <span>Validate Code</span>
                              <FontAwesomeIcon
                                className="pl-1"
                                icon={faCheckDouble}
                              />
                            </Button>
                            <div className="mt-3 pb-4 mb-5">
                              <div className="text-center mt-1 float-left">
                                <Button
                                  type="button"
                                  variant="contained"
                                  onClick={() => goto(1)}
                                  className="btn btn-primary"
                                >
                                  <FontAwesomeIcon
                                    icon={faArrowLeft}
                                    className="mr-1 text-white"
                                  />{" "}
                                  <span className="text-white">Back</span>
                                </Button>
                              </div>
                              <div className="text-center mt-1 float-right  text-white">
                                <Button
                                  type="button"
                                  variant="contained"
                                  className="btn btn-primary"
                                  disabled={!isCodeValidated}
                                  onClick={() => goto(3)}
                                >
                                  <span className="text-white">Next</span>
                                  <FontAwesomeIcon
                                    icon={faArrowRight}
                                    className="ml-1 text-white"
                                  />
                                </Button>
                              </div>
                            </div>
                          </div>
                        )}
                      </Col>
                    </Form>
                  </Col>
                </div>
              )}
              {step3 && (
                <div>
                  <Col xs={12} className="mt-1 text-center mb-2">
                    <h5>STEP 3: Personal Details</h5>
                  </Col>
                  <Col xs={12}>
                    <Form onSubmit={(e) => saveProfile(e)} autoComplete="off">
                      <Row>
                        <Col xs={12}>
                          <Row>
                            <Col xs={12}>
                              <Card className="ml-5 mr-5">
                                <CardBody className="pt-1">
                                  <div>
                                    <TextField
                                      name="first_name"
                                      label="First Name"
                                      value={firstName}
                                      onChange={(e) =>
                                        setFirstName(e.target.value)
                                      }
                                      fullWidth
                                      required
                                    />
                                  </div>
                                  <div>
                                    <TextField
                                      name="last_name"
                                      label="Last Name"
                                      value={lastName}
                                      onChange={(e) =>
                                        setLastName(e.target.value)
                                      }
                                      fullWidth
                                      required
                                    />
                                  </div>
                                  <div>
                                    <TextField
                                      type="number"
                                      name="age"
                                      label="Age"
                                      value={age}
                                      onChange={(e) => setAge(e.target.value)}
                                      fullWidth
                                      required
                                    />
                                  </div>
                                  <div>
                                    <GenderSelect
                                      gender={gender}
                                      setGender={setGender}
                                    />
                                  </div>
                                  <div>
                                    <TextField
                                      name="mobile"
                                      type="number"
                                      label="Mobile Number"
                                      value={mobileNumber || ""}
                                      onChange={(e) =>
                                        setMobileNumber(e.target.value)
                                      }
                                      fullWidth
                                      required
                                    />
                                  </div>
                                  <div>
                                    <TextField
                                      type="email"
                                      name="email"
                                      label="Email Address"
                                      value={email}
                                      fullWidth
                                      disabled={true}
                                    />
                                  </div>
                                  <div>
                                    <TextField
                                      name="zipcode"
                                      label="Zipcode"
                                      value={zipcode || ""}
                                      onChange={(e) =>
                                        setZipcode(e.target.value)
                                      }
                                      fullWidth
                                      required
                                    />
                                  </div>
                                  <div>
                                    <TextField
                                      label="Complete Address"
                                      name="complete_address"
                                      value={specificAddress}
                                      onChange={(e) =>
                                        setSpecificAddress(e.target.value)
                                      }
                                      fullWidth
                                      required
                                    />
                                  </div>
                                </CardBody>
                              </Card>
                            </Col>
                          </Row>
                        </Col>
                        <Col xs={12} className="mt-2">
                          {isProcessing ? (
                            <div
                              className="text-center mt-1"
                              style={{ minHeight: "100px" }}
                            >
                              <Spinner
                                className="mt-4"
                                style={{ width: "2rem", height: "2rem" }}
                                color="primary"
                              />
                              <br />
                              <span className="mt-1">Processing...</span>
                            </div>
                          ) : (
                            <div>
                              <div className="text-center mt-1 float-left">
                                <Button
                                  type="button"
                                  variant="contained"
                                  onClick={() => goto(2)}
                                  className="btn btn-primary"
                                >
                                  <FontAwesomeIcon
                                    icon={faArrowLeft}
                                    className="mr-1 text-white"
                                  />{" "}
                                  <span className="text-white">Back</span>
                                </Button>
                              </div>
                              <div className="text-center mt-1 mb-2 float-right  text-white">
                                <Button
                                  type="submit"
                                  variant="contained"
                                  className="btn btn-primary"
                                >
                                  <span className="text-white">Next</span>
                                  <FontAwesomeIcon
                                    icon={faArrowRight}
                                    className="ml-1 text-white"
                                  />
                                </Button>
                              </div>
                            </div>
                          )}
                        </Col>
                      </Row>
                    </Form>
                  </Col>
                </div>
              )}
              {step4 && (
                <div>
                  <Col md={{ size: 10, offset: 1 }}>
                    <Form
                      onSubmit={(e) => e.preventDefault()}
                      autoComplete="off"
                    >
                      <Row>
                        <Col xs={12} className="mt-1 text-center mb-2">
                          <h5>STEP 4: Account Details</h5>
                        </Col>
                        <Col xs={12}>
                          <Row>
                            <Col xs={12} className="pl-4 pr-4 mb-2">
                              <TextField
                                name="username"
                                placeholder="Username"
                                value={username}
                                onChange={(e) => setUsername(e.target.value)}
                                label="Username"
                                fullWidth
                                required
                              />
                            </Col>
                            <Col xs={12} className="pl-4 pr-4 mb-2">
                              <TextField
                                type={isShowPass ? "text" : "password"}
                                name="password"
                                value={password}
                                onChange={(e) => setPassword(e.target.value)}
                                placeholder="Password"
                                label="Password"
                                fullWidth
                                required
                                InputProps={{
                                  endAdornment: (
                                    <InputAdornment position="end">
                                      <IconButton onClick={toggleShowPassword}>
                                        {isShowPass ? (
                                          <Visibility />
                                        ) : (
                                          <VisibilityOff />
                                        )}
                                      </IconButton>
                                    </InputAdornment>
                                  ),
                                }}
                              />
                            </Col>
                            <Col xs={12} className="pl-4 pr-4 mb-2">
                              <TextField
                                type={isShowRePass ? "text" : "password"}
                                name="repassword"
                                value={repassword}
                                onChange={(e) => setRepassword(e.target.value)}
                                placeholder="Re-type Password"
                                label="Re-type Password"
                                fullWidth
                                required
                                InputProps={{
                                  endAdornment: (
                                    <InputAdornment position="end">
                                      <IconButton
                                        onClick={toggleShowRePassword}
                                      >
                                        {isShowRePass ? (
                                          <Visibility />
                                        ) : (
                                          <VisibilityOff />
                                        )}
                                      </IconButton>
                                    </InputAdornment>
                                  ),
                                }}
                              />
                            </Col>
                          </Row>
                        </Col>
                        <Col xs={12} className="pl-3 pr-3 mt-2 mb-2 pb-2">
                          {isProcessing ? (
                            <div
                              className="text-center mt-1"
                              style={{ minHeight: "50px" }}
                            >
                              <Spinner
                                className="mt-1"
                                style={{ width: "2rem", height: "2rem" }}
                                color="primary"
                              />
                              <br />
                              <span className="mt-1">Processing...</span>
                            </div>
                          ) : (
                            <div>
                              <div className="text-center mt-1 float-left">
                                <Button
                                  type="button"
                                  variant="contained"
                                  onClick={() => goto(3)}
                                  className="btn btn-primary"
                                >
                                  <FontAwesomeIcon
                                    icon={faArrowLeft}
                                    className="mr-1 text-white"
                                  />{" "}
                                  <span className="text-white">Back</span>
                                </Button>
                              </div>
                              <div className="text-center mt-1 float-right  text-white">
                                <Button
                                  type="submit"
                                  variant="contained"
                                  className="btn btn-primary"
                                  onClick={() => submitPassengerData()}
                                >
                                  <span className="text-white">Submit</span>
                                  <FontAwesomeIcon
                                    icon={faCheck}
                                    className="ml-1 text-white"
                                  />
                                </Button>
                              </div>
                            </div>
                          )}
                        </Col>
                      </Row>
                    </Form>
                  </Col>
                </div>
              )}
            </Card>
          </Col>
        </Row>
      </Card>
    </Container>
  );
};

export default Register;
